<template>
<!-- eslint-disable -->
  <div>
    <validation-observer 
      ref="inventoryEdit"
      #default="{invalid}">
      <b-modal
        id="editInventory"
        ref="editModal"
        hide-footer
        title="Editar Inventario"
        scrollable
        >

        <ErrorValidation 
          v-if="validationErrors" 
          :errors="validationErrors" 
        />

        <div class="d-block">
          
          <b-form @submit.prevent="updateInventory">
                        
            <b-form-row>
              <!-- Código -->              
              <b-col md="6">
                <b-form-group
                  label="Código"
                  label-for="code"
                >
                  <validation-provider
                    #default="{ errors }"
                    name="código"
                    rules="required|alpha-dash"
                  >
                    <b-form-input
                      id="code"
                      v-model="inventory.code"                      
                      type="text"                      
                      placeholder="Código"
                      :state="errors.length > 0 ? false:null"
                    />
                    <small class="text-danger">{{ errors[0] }}</small>
                  </validation-provider>
                </b-form-group>
              </b-col>

              <!-- Referencia -->
              <b-col md="6">
                <b-form-group
                  label="Referencia"
                  label-for="reference"
                >
                  <validation-provider
                    #default="{ errors }"
                    name="referencia"
                    rules="required"
                    >
                    <b-form-input
                      id="reference"
                      v-model="inventory.reference"
                      type="text"                      
                      placeholder="Referencia"
                      :state="errors.length > 0 ? false:null"
                    />
                    <small class="text-danger">{{ errors[0] }}</small>
                  </validation-provider>
                </b-form-group>
              </b-col>
            </b-form-row>       
                        
            <b-form-row>
              <!-- Linea -->              
              <b-col md="6">
                <b-form-group
                  label="Linea"
                  label-for="line"
                >
                  <validation-provider
                    #default="{ errors }"
                    name="linea"
                    rules="required"
                    >
                    <b-form-input
                      id="line"
                      v-model="inventory.line"
                      type="text"                      
                      placeholder="Linea"
                      :state="errors.length > 0 ? false:null"
                    />
                    <small class="text-danger">{{ errors[0] }}</small>
                  </validation-provider>
                </b-form-group>
              </b-col>

              <!-- Costo -->
              <b-col md="6">
                <b-form-group
                  label="Costo"
                  label-for="cost"
                >
                  <validation-provider
                    #default="{ errors }"
                    name="costo"
                    rules="required|integer"
                    >
                    <b-form-input
                      id="cost"
                      v-model="inventory.cost"
                      type="text"                      
                      placeholder="Costo"
                      :state="errors.length > 0 ? false:null"
                    />
                    <small class="text-danger">{{ errors[0] }}</small>
                  </validation-provider>
                </b-form-group>
              </b-col>
            </b-form-row>

            <b-form-row>
              <!-- Cantidad -->              
              <b-col md="6">
                <b-form-group
                  label="Cantidad"
                  label-for="cantidad"
                >
                  <validation-provider
                    #default="{ errors }"
                    name="cantidad"
                    rules="required|integer"
                    >
                    <b-form-input
                      id="cantidad"
                      v-model="inventory.stock"
                      type="text"                      
                      placeholder="Cantidad"
                      :state="errors.length > 0 ? false:null"
                    />
                    <small class="text-danger">{{ errors[0] }}</small>
                  </validation-provider>
                </b-form-group>
              </b-col>

              <!-- Cantidad Minima -->
              <b-col md="6">
                <b-form-group
                  label="Cantidad Mínima"
                  label-for="cantidad_minima"
                >
                  <validation-provider
                    #default="{ errors }"
                    name="cantidad_minima"
                    rules="required|integer"
                    >
                    <b-form-input
                      id="cantidad_minima"
                      v-model="inventory.min_stock"
                      type="text"                      
                      placeholder="Cantidad Mínima"
                      :state="errors.length > 0 ? false:null"
                    />
                    <small class="text-danger">{{ errors[0] }}</small>
                  </validation-provider>
                </b-form-group>
              </b-col>
            </b-form-row>

            <b-form-row>
              <!-- Ubicación -->              
              <b-col md="12">
                <b-form-group
                  label="Ubicación"
                  label-for="ubicacion"
                >
                  <validation-provider
                    #default="{ errors }"
                    name="ubicacion"
                    rules="required|alpha-num"
                    >
                    <b-form-input
                      id="ubicacion"
                      v-model="inventory.location"
                      type="text"                      
                      placeholder="Ubicación"
                      :state="errors.length > 0 ? false:null"
                    />
                    <small class="text-danger">{{ errors[0] }}</small>
                  </validation-provider>
                </b-form-group>
              </b-col>              
            </b-form-row>
           
            <hr>

            <div class="text-right">
              <button
                type="button"
                class="btn btn-danger btn-sm mx-1"
                @click="hideModal()"
              >
                Cancelar
              </button>
              <button
                type="submit"
                class="btn btn-primary btn-sm"
                :disabled="invalid"
              >
                <span class="fa fa-check" /> Actualizar
              </button>
            </div>
          </b-form>
        </div>
      </b-modal>
    </validation-observer>
  </div>
</template>

<script>

/*eslint-disable*/

// Notificaciones
import ToastificationContent from '@core/components/toastification/ToastificationContent.vue'

// Servicios
import * as inventoryService from '@/services/store/store'

// Bootstrap Vue
import {
  BCard, BRow, BCol, BFormInput, BButton, BTable, BMedia, BAvatar, BLink,
  BBadge, BDropdown, BDropdownItem, BPagination,
} from 'bootstrap-vue'

export default {

  props: {
    inventory: {
      type: Object,
      default: () => {},
    },
  },

  components: {
    BCard,
    BRow,
    BCol,
    BFormInput,
    BButton,
    BTable,
    BMedia,
    BAvatar,
    BLink,
    BBadge,
    BDropdown,
    BDropdownItem,
    BPagination,
    ToastificationContent,
  },

  data() {
    return {
      validationErrors: '',
    }
  },

  methods: {

    async updateInventory() {
      try {
        const response = await inventoryService.updateInventory(this.inventory.id, this.inventory)

        if (response.status === 200) {          
          this.hideModal()
          this.$emit('reload')
          this.showToast('Actualizado', 'CheckIcon', 'Producto Actualizado', 'success')
        }
      } catch (error) {
         if (error.response.status === 422) {
            this.validationErrors = error.response.data.errors;
          } else {
            this.showToast('Advertencia!', 'AlertCircleIcon', 'Ocurrió un error al actualizar el', 'warning')
          }
      }
    },

    hideModal() {
      this.$refs.inventoryEdit.reset()
      this.validationErrors = ''
      this.$refs.editModal.hide()
      this.$emit('reload')       
    },

    showToast(title, icon, text, variant) {
      this.$toast({
        component: ToastificationContent,
        props: {
          title,
          icon,
          text,
          variant,
        },
      })
    },

  },

}

</script>
